export const FETCH_PEOPLE = 'FETCH_PEOPLE';
export const FETCH_PEOPLE_COMPLETED = 'FETCH_PEOPLE_COMPLETED';
export const FETCH_PEOPLE_FAILED = 'FETCH_PEOPLE_FAILED';

export const FETCH_TRENDING_PEOPLE = 'FETCH_TRENDING_PEOPLE';
export const FETCH_TRENDING_PEOPLE_COMPLETED = 'FETCH_TRENDING_PEOPLE_COMPLETED';
export const FETCH_TRENDING_PEOPLE_FAILED = 'FETCH_TRENDING_PEOPLE_FAILED';

function getPeopleCompleted(people) {
    return {
      type: FETCH_PEOPLE_COMPLETED,
      people,
      receivedAt: Date.now(),
    }
}
  
function getPeopleFailed () {
    return {
      type: FETCH_PEOPLE_FAILED,
      receivedAt: Date.now(),
    }
}

export function getPeople(handle, fetch) {
    return async (dispatch, getState) => {
        try {
            const resp = await fetch('/graphql', {
                body: JSON.stringify({
                  query: `
                    {
                        people (q: "${handle}") {
                            id
                            handle
                            name
                            url
                            imageUrl
                            description
                            followersCount
                            location
                        }
                    }
                  `,
                })
            })
            const {data} = await resp.json()
            if (!data || !data.people) {
                return dispatch(getPeopleFailed())
            }
            dispatch(getPeopleCompleted(data.people))
        } catch (e) {
            dispatch(getPeopleFailed());
        }
    }
}

function getTrendingPeopleCompleted(people) {
    return {
      type: FETCH_PEOPLE_COMPLETED,
      people,
      receivedAt: Date.now(),
    }
}
  
function getTrendingPeopleFailed () {
    return {
      type: FETCH_PEOPLE_FAILED,
      receivedAt: Date.now(),
    }
}

export function getTrendingPeople(handle, fetch) {
    return async (dispatch, getState) => {
        try {
            const resp = await fetch('/graphql', {
                body: JSON.stringify({
                  query: `
                    {
                        trendingPeople {
                            handle
                            requestCount
                            name
                            path
                            total
                        }
                    }
                  `,
                })
            })
            const {data} = await resp.json()
            if (!data || !data.trendingPeople) {
                return dispatch(getTrendingPeopleFailed())
            }
            dispatch(getTrendingPeopleCompleted(data.trendingPeople))
        } catch (e) {
            dispatch(getTrendingPeopleFailed());
        }
    }
}


