export default function calls (calls = {page : 1, pages: 1, total : 0, isFetching: true, calls : []}, action) {
  // return {page : 1, pages: 1, total : 0, isFetching: true, calls : allCalls}
  switch (action.type) {

    case FETCH_CALLS_COMPLETED:
      return {...calls, ...action.calls}

    case FETCH_CALLS_FAILED:
      return {...calls}

    default:
      return calls
  }
}


