import { SET_SQUEEZE } from '../actions/ui'

export default function ui (ui = {columns: 3}, action) {
  switch (action.type) {
    case SET_SQUEEZE:
      if (action.width < 620) {
        return {...ui, columns: 1}
      } else {
        return {...ui, columns: 3}
      }
    default:
      return ui
  }
}


