import { GET_INBOX_COMPLETED, GET_INBOX_FAILED, GET_INBOX } from '../actions/user'
import moment from 'moment'

export default function inbox (inbox = {page : 1, pages: 1, total : 0, isFetching: true, inbox : []}, action) {
  switch (action.type) {
    case GET_INBOX_COMPLETED:
      return {...inbox, ...action.inbox}

    case GET_INBOX_FAILED:
      return {...inbox}

    default:
      return inbox
  }
}


