export const fetchUser = (fetch) => {
  return async (dispatch, getState) => {
    const resp = await fetch('/graphql', {
      body: JSON.stringify({
        query: `{
          me {
            ${meDetails}
          }
        }`,
      }),
    })
    const {data} = await resp.json()
    if (!data || !data.me) {
      return dispatch(getUserFailed())
    }
    return dispatch(getUserCompleted(data.me))
  }
}

export const fetchInbox = (fetch) => {
  return async (dispatch, getState) => {
    const resp = await fetch('/graphql', {
      body: JSON.stringify({
        query: `{
          getInbox {
            path
            rate
            name
            pic
            shortURL
          }
        }`,
      }),
    })
    const {data} = await resp.json()
    if (!data || !data.getInbox) {
      return dispatch(getInboxFailed())
    }
    return dispatch(getInboxCompleted({
        page : 1, 
        pages: 1, 
        total : 0, 
        isFetching: false,
        inbox: data.getInbox
      })
    )
  }
}

export function fetchBTToken(fetch) {
  return async dispatch => {
    const resp = await fetch('/graphql', {
      body: JSON.stringify({
        query: `{
          credits {
            clientToken
          }
        }`,
      }),
    });
    const { data } = await resp.json();

    // if (!data || !data.credits) throw new Error('Failed to load credits.');

    dispatch(getBTTokenCompleted(data.credits.clientToken));
  };
}

export function fetchPhonesToBuy(fetch) {
  return async dispatch => {
    const resp = await fetch('/graphql', {
      body: JSON.stringify({
        query: `{
          getAvailablePhones {
            number,
            name
          }
        }`,
      }),
    }).then((resp)=>(resp.json()))
    dispatch(getPhonesToBuyCompleted(resp.data.getAvailablePhones));
  };
}

export function submitPaymentInformation(customer, fetch) {
  const { details, nonce } = customer;
  return async (dispatch, getState) => {
    try {
      const user = await fetch('/graphql', {
        body: JSON.stringify({
          query: `
            mutation {
              updatePayment (nonce: "${nonce}", type: "${customer.type}") {
                ${meDetails}
              }
            }
          `,
        }),
      });
      const {data} = await resp.json()
      if (!data || !data.updatePayment) {
        return dispatch(submitPaymentInformationFailed())
      }
      dispatch(submitPaymentInformationCompleted(data.updatePayment));
    } catch (e) {
      dispatch(submitPaymentInformationFailed())
    }
  };
}

