export function submitUser (user, fetch) {
  return async (dispatch, getState) => {
    try {
      const gInput = JSON.stringify(user, null, 5).replace(/     "/g, '').replace(/":/g, ':')
      const updatedUser = await fetch('/graphql', {
        body: JSON.stringify({
          query: `
            mutation {
              updateMe (input: ${gInput}) {
                ${meDetails}
              }
            }
          `,
        }),
      })
      const {data} = await resp.json()

      if (!data || !data.updateMe) {
        return dispatch(submitUserFailed())
      }
      return dispatch(submitUserCompleted(data.updateMe))
    } catch (e) {

      dispatch(submitUserFailed());
    }
  };
}

export function submitSchedule (schedule, fetch) {
  return async (dispatch, getState) => {
    try {
      const gInput = JSON.stringify(schedule, null, 5).replace(/     "/g, '').replace(/":/g, ':')
      const updatedUser = await fetch('/graphql', {
        body: JSON.stringify({
          query: `
            mutation {
              updateMySchedule (input: ${gInput}) {
                ${meDetails}
              }
            }
          `,
        }),
      })
      dispatch(submitUserCompleted(updatedUser));
    } catch (e) {
      console.log(e)
      dispatch(submitUserFailed());
    }
  };
}
