export function buyCredits(amount, fetch) {
  return async (dispatch, getState) => {
    try {
      const user = await fetch('/graphql', {
        body: JSON.stringify({
          query: `
            mutation {
              buyCredits (amount: ${amount}) {
                ${meDetails}
              }
            }
          `,
        }),
      }).then((resp)=>(resp.json()))

      if (user.errors && user.errors.length) {
        throw user.errors[0]
      }
      dispatch(buyCreditsCompleted(user));
    } catch (e) {
      dispatch(buyCreditsFailed(e));
    }
  };
}
