export const PLACE_CALL = 'PLACE_CALL'
export const PLACE_CALL_COMPLETED = 'PLACE_CALL_COMPLETED'
export const PLACE_CALL_FAILED = 'PLACE_CALL_FAILED'

export const HANGUP = 'HANGUP'
export const HANGUP_COMPLETED = 'HANGUP_COMPLETED'
export const HANGUP_FAILED = 'HANGUP_FAILED'

export const CALL_TICKED = 'CALL_TICKED'
export const CALL_CONNECTING = "CALL_CONNECTING"
export const CALL_CONNECTION_COMPLETED = 'CALL_CONNECTION_COMPLETED'
export const CALL_CONNECTION_FAILED = 'CALL_CONNECTION_FAILED'
export const CALL_DISCONNECTON_COMPLETED = "CALL_DISCONNECTON_COMPLETED"
export const CALL_HANGUP_COMPLETED = 'CALL_HANGUP_COMPLETED'

export const FETCH_CALLS = 'FETCH_CALLS'
export const FETCH_CALLS_COMPLETED = 'FETCH_CALLS_COMPLETED'
export const FETCH_CALLS_FAILED = 'FETCH_CALLS_FAILED'

function getUser () {
  return {
    type: GET_USER
  }
}

function callConnecting () {
  return {
    type : CALL_CONNECTING,
    receivedAt: Date.now()
  }
}

function callHangupCompleted () {
  return {
    type: CALL_HANGUP_COMPLETED,
    receivedAt: Date.now()
  }
}

function callConnectionCompleted (call) {
  return {
    type: CALL_CONNECTION_COMPLETED,
    call,
    receivedAt: Date.now()
  }
}

function callConnectionFailed (error) {
  return {
    type: CALL_CONNECTION_FAILED,
    receivedAt: Date.now(),
    error: error
  }
}

function callDisconnectionCompleted () {
  return {
    type: CALL_DISCONNECTON_COMPLETED,
    receivedAt: Date.now()
  }
}

function fetchCalls () {
  return {
    type: FETCH_CALLS,
    receivedAt: Date.now()
  }
}

function fetchCallsCompleted (calls) {
  return {
    type: FETCH_CALLS_COMPLETED,
    calls,
    receivedAt: Date.now()
  }
}

function fetchCallsFailed () {
  return {
    type: FETCH_CALLS_FAILED,
    receivedAt: Date.now()
  }
}

function tickCall () {
  return {
    type: CALL_TICKED
  }
}

function hasTwilioBeenSetup () {
  try {
    Twilio.Device.status();
    return true;
  } catch(e) {
    console.log(e)
    return false;
  }
}

function setCookie(cname, cvalue, exdays) {
    var d = new Date();
    d.setTime(d.getTime() + (exdays*24*60*60*1000));
    var expires = "expires="+ d.toUTCString();
    document.cookie = cname + "=" + cvalue + ";" + expires + ";path=/";
}

function setupHandlers (token, user, dispatch) {
  Twilio.Device.setup(token, { debug: true })

  Twilio.Device.ready(function (device) {
    Twilio.Device.connect()
  })

  Twilio.Device.error(function (error) {
    console.log(error)
  })

  Twilio.Device.connect(function (conn) {  
    const {CallSid} = conn.parameters
    dispatch(callConnectionCompleted({callSid: CallSid}))
    // Drop a cookie
    setCookie('callsid', CallSid, 30)
  })

  Twilio.Device.disconnect(function (conn) {
    dispatch(callDisconnectionCompleted())
    if (!user) {
      // Login damn it
       
    }
  })
}

export function makeCall (token, user = null) {
  return async (dispatch) => {
    dispatch(callConnecting())
    try {      
      if (!hasTwilioBeenSetup()) {
        setupHandlers(token, user, dispatch)
      } else {
        if (Twilio.Device.status() === "ready") {
          Twilio.Device.connect();
        } else if (Twilio.Device.status() === "offline") {
          alert("Your session has timed out. Please refresh the page.");
        } else {
          alert("Could not make the call. Please refresh the page.");
        }
      }
    } catch (e) {
      dispatch(callConnectionFailed(e.message));
    }
  }
}

export function getCalls (page = 1, fetch) {
  return async (dispatch) => {
    try {
      const response = await fetch('/graphql', {
        body: JSON.stringify({
          query: `
            {
              calls(page: ${page}) {
                calls {
                  amount
                  incoming
                  duration
                  rate
                  providerName
                  providerPic
                  recordingUrl
                  createdAt
                  shortURL
                  path
                }
                total
                page
                pages
              }
            }
          `,
        })
      })
      const respJson = await response.json()      
      const calls = respJson.data.calls
      dispatch(fetchCallsCompleted(calls))
    } catch (e) {
      console.log(e)
      dispatch(fetchCallsFailed(e.message));
    }
  }
}

export function endCall (conn) {
  return dispatch => {
    dispatch(callHangupCompleted());
    Twilio.Device.disconnectAll();
    setTimeout(Twilio.Device.destroy, 500);
  }
}

export function addOneSecondToCallDuration () {
  return dispatch => {
    dispatch(tickCall());
  }
}



