import { FETCH_SERVICE_COMPLETED, FETCH_SERVICE_FAILED } from '../actions/services'

export default function service(state = {errorCode: null}, action) {
  switch (action.type) {
    case FETCH_SERVICE_COMPLETED:
      return {...action.service, errorCode: null}
    case FETCH_SERVICE_FAILED:
      return {errorCode: action.errorCode, ...action.service, schedule: action.schedule}
    default:
      return state;
  }
}



