import { GET_USER_COMPLETED } from './user';

export const FETCH_SERVICE = 'FETCH_SERVICE';
export const FETCH_SERVICE_COMPLETED = 'FETCH_SERVICE_COMPLETED';
export const FETCH_SERVICE_FAILED = 'FETCH_SERVICE_FAILED';

export const UPDATE_SERVICE = 'UPDATE_SERVICE';
export const UPDATE_SERVICE_COMPLETED = 'UPDATE_SERVICE_COMPLETED';
export const UPDATE_SERVICE_FAILED = 'UPDATE_SERVICE_FAILED';

export const REMOVE_SERVICE = 'REMOVE_SERVICE';
export const REMOVE_SERVICE_COMPLETED = 'REMOVE_SERVICE_COMPLETED';
export const REMOVE_SERVICE_FAILED = 'REMOVE_SERVICE_FAILED';

export const ADD_SERVICE_COMPLETED = 'ADD_SERVICE_COMPLETED';
export const ADD_SERVICE_FAILED = 'ADD_SERVICE_FAILED';

import {COULD_NOT_FIND_SERVICE} from '../lib/errors'

function removeService(service) {
  return {
    type: REMOVE_SERVICE,
    service,
    receivedAt: Date.now(),
  };
}

function removeServiceCompleted(service) {
  return {
    type: REMOVE_SERVICE_COMPLETED,
    service,
    receivedAt: Date.now(),
  };
}

function removeServiceFailed(service) {
  return {
    type: REMOVE_SERVICE_FAILED,
    service,
    receivedAt: Date.now(),
  };
}

function updateServiceCompleted(service) {
  return {
    type: UPDATE_SERVICE_COMPLETED,
    service,
    receivedAt: Date.now(),
  };
}

function addServiceCompleted(service) {
  return {
    type: ADD_SERVICE_COMPLETED,
    service,
    receivedAt: Date.now(),
  };
}

function addServiceFailed(message) {
  return {
    type: ADD_SERVICE_FAILED,
    message,
    receivedAt: Date.now(),
  };
}

function getService(shortURL) {
  return {
    type: FETCH_SERVICE,
    shortURL,
    receivedAt: Date.now(),
  };
}

function fetchServiceCompleted(service) {
  return {
    type: FETCH_SERVICE_COMPLETED,
    service,
    receivedAt: Date.now(),
  };
}

function fetchServiceFailed ({errorCode, service, schedule}) {
  return {
    type: FETCH_SERVICE_FAILED,
    errorCode, 
    service, 
    schedule,
    receivedAt: Date.now(),
  };
}

