export function fetchService (shortURL, fetch) {
  return async (dispatch, getState) => {
    try {
      const response = await fetch('/graphql', {
        body: JSON.stringify({
          query: `
            {
              getCallableService (shortURL: "${shortURL}") {
                token
                imgUrl
                service {
                  _id
                  path
                  name
                  shortURL
                  destination
                  active
                  rate
                  payer
                }
              }
            }
          `,
        })
      })
      const respJson = await response.json()
      const {errors} = respJson
      if (errors && errors.length) {
        return dispatch(fetchServiceFailed(errors[0]))
      }      

      const callableService = respJson.data.getCallableService
      if (!callableService) {
        return dispatch(fetchServiceFailed(COULD_NOT_FIND_SERVICE))
      }
      const service = {
        ...callableService.service, 
        schedule: callableService.schedule, 
        token: callableService.token,
        imgUrl: callableService.imgUrl,
      }
      dispatch(fetchServiceCompleted(service))
    } catch (e) {
      dispatch(fetchServiceFailed(e.message));
    }
  }
}

export function addNewService (path, name, rate, payer, fetch) {
  return async (dispatch, getState) => {
    try {
      const response = await fetch('/graphql', {
        body: JSON.stringify({
          query: `
            mutation {
              addNewService (path: "${path}", name: "${name}", rate: ${rate}, payer: ${payer}) {
                _id
                path
                name
                rate
                payer
                shortURL
                destination
                active              
              }
            }
          `,
        }),
      })
      const respJson = await response.json()
      dispatch(addServiceCompleted(respJson.data.addNewService))
    } catch (e) {
      console.log(e)
      dispatch(addServiceFailed(e.message));
    }
  }
}

export function updateService(service, fetch) {
  return async (dispatch, getState) => {
    const { _id, path, rate, name, payer, destination, active, expires } = service;
    try {
      const resp = await fetch('/graphql', {
        body: JSON.stringify({
          query: `
            mutation {
              updateService (_id: "${_id}", 
               path: "${path}", 
               name: "${name}", 
               rate: ${rate}, 
               payer: ${payer}, 
               destination: ${destination}, 
               expires: ${expires}, 
               active: ${active}) {
                _id
                path
                name
                shortURL
                destination
                active
                rate
                payer
                expires
              }
            }
          `,
        }),
      });
      const newService = await resp.json()
      dispatch(updateServiceCompleted(newService.data.updateService));
    } catch (e) {
      console.log(e);
    }
  };
}

export function deleteService(service, fetch) {
  return async (dispatch, getState) => {
    const { _id } = service;
    try {
      const resp = await fetch('/graphql', {
        body: JSON.stringify({
          query: `
            mutation {
              deleteService (_id: "${_id}") {
                _id
              }
            }
          `,
        }),
      })
      dispatch(removeServiceCompleted(service))
    } catch (e) {
      dispatch(removeServiceFailed(service))
    }
  }
}



