
import { FETCH_PEOPLE_COMPLETED, FETCH_PEOPLE_FAILED } from '../actions/people'

export default function people (people = [], action) {
  switch (action.type) {
    case FETCH_PEOPLE_COMPLETED:
      return [...action.people]
    case FETCH_PEOPLE_FAILED:
      return []
    default:
      return people
  }
}


