export const loggedOut = () => ({
  type: LOGOUT
})

export const logout = (fetch) => {
  return (dispatch) => {
    return fetch('/logout', {
      method: 'POST',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      },
      credentials: 'same-origin'
    })
    .then(json => {
      dispatch(loggedOut())     
    })
  }
}

export const navigateTo = (path, params) => {
  return (dispatch) => {
    try {
      history.push(path, params)
    } catch(e) {
      console.log(e)
    }
  }
}



