import {CALL_CONNECTING, CALL_CONNECTION_COMPLETED, CALL_CONNECTION_FAILED, CALL_DISCONNECTON_COMPLETED, CALL_HANGUP_COMPLETED, CALL_TICKED} from '../actions/call'
import {CLAIM_EARNINGS_COMPLETED} from '../actions/user'
import moment from 'moment'

export default function call (call = {status: 'disconnected', duration: "00:00"}, action) {
  // return Object.assign({}, call, {status : 'connected'})
  switch (action.type) {

    case CALL_CONNECTING:
      return {...call, status : 'connecting'}

    case CALL_CONNECTION_COMPLETED:
      return {...call, ...action.call, status : 'connected', started: new Date()}

    case CALL_CONNECTION_FAILED:
      return {...call, status : 'failed'}

    case CALL_HANGUP_COMPLETED:
      return {...call, status : 'disconnected', duration: '00:00', needCaptchaReset: true}

    case CALL_DISCONNECTON_COMPLETED:
      return {...call, status : 'disconnected', duration: '00:00', needCaptchaReset: true}

    case CLAIM_EARNINGS_COMPLETED:
      return {...call, callSid : null}

    case CALL_TICKED:
      const durationMS = moment().diff(moment(call.started))
      const durationStr = moment(durationMS).format("mm:ss")
      return {...call, duration: durationStr}

    default:
      return call
  }
}


