
import { GET_PHONES_COMPLETED, GET_PHONES_FAILED } from '../actions/user'

export default function phones (phones = [], action) {
  switch (action.type) {
    case GET_PHONES_COMPLETED:

      return [...action.phones.splice(0, 8)]

    case GET_PHONES_FAILED:
      return [...phones]

    default:
      return phones
  }
}


