import { ADD_SERVICE_COMPLETED, UPDATE_SERVICE_COMPLETED, REMOVE_SERVICE_COMPLETED } from '../actions/services'
import { GET_USER_COMPLETED, GET_BT_CLIENT_TOKEN_COMPLETED, SUBMIT_PHONE_COMPLETED } from '../actions/user'
import { LOGOUT } from '../actions/app'

export default function user(state = {}, action) {
  switch (action.type) {
    case GET_USER_COMPLETED:
      return {...state, ...action.user}
    case SUBMIT_PHONE_COMPLETED:
      return {...state, ...action.user}
    case ADD_SERVICE_COMPLETED:
      const newServices = [...state.provider.services, action.service]
      return {...state, provider: {...state.provider, services: newServices}}
    case UPDATE_SERVICE_COMPLETED:
      const updatedServices = [...state.provider.services].map((s) => {
        if (s._id === action.service._id) {
          return action.service
        }
        return s
      })
      return {...state, provider: {...state.provider, services: updatedServices}}
    case REMOVE_SERVICE_COMPLETED:
      const servicesAfterRemoval = [...state.provider.services].filter(s => (s._id !== action.service._id))
      return {...state, provider: {...state.provider, services: servicesAfterRemoval}}
    case GET_BT_CLIENT_TOKEN_COMPLETED:
      return {...state, clientToken: action.clientToken}
    case LOGOUT:
      return {}
    default:
      return state;
  }
}



